import request from "../request"

export function allMv(data){
    return request({
        url: "/mv/all",
        method: "GET",
        data
      });
}

export function firstMv(data ={limit:100}){
    return request({
        url: `/mv/first`,
        method: "GET",
        data
      });
}

export function wangyiMv(data = {limit:100}){
  return request({
      url: `/mv/exclusive/rcmd`,
      method: "GET",
      data
    });
}

// /

export function mvDetail(data = {mvid:5436712}){
  return request({
      url: `/mv/detail`,
      method: "GET",
      data
    });
}



export function mvUrl(data = {id : 5436712}){
  return request({
      url: `/mv/url`,
      method: "GET",
      data
    });
}
