import { allMv, firstMv, mvUrl } from "@/api/mv/index.js";
import { Toast } from "vant";
export default {
  name: "Mv",
  components: {},

  data() {
    return {
      mv: [],
      mvObj: {}
    };
  },

  computed: {},

  async created() {
    console.log(this.$route);
    var {
      data
    } = await firstMv();
    this.mv = data;
    console.log(data);
  },

  methods: {
    goHome() {
      this.$router.push({
        path: "/home"
      });
    },

    mvChange(index) {
      console.log(index);
      this.mvClick(this.mv[index]);
    },

    async mvClick(item) {
      console.log(item);
      let {
        data
      } = await mvUrl({
        id: item.id
      });
      console.log(data);
      this.mvObj = data;

      if (!this.mvObj.url) {
        Toast({
          message: "暂无资源",
          icon: "warning"
        });
      }

      console.log(this.mvObj);
    }

  }
};